.container-tab {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  width: 100%;
  margin: 0 auto;
}

.tabtitle-mobile {
  display: none;
}

.tabtitle {
  display: flex;
  flex-direction: column;
  width: 180px;
  margin-right: 30px;
}

.tabtitle button {
  width: 180px;
  background-color: inherit;
  padding: 10px 16px;
  text-align: left;
  border: none;
  outline: none;
}

.tabtitle button:hover {
  background-color: #ddd;
}

.tabtitle button.active {
  background-color: #ccc;
}

.tabcontent {
  width: 100%;
  height: 75vh;
  overflow-y: auto;
  overflow-x: hidden !important;
  border: 2px solid #eeeeee;
  padding: 10px;

  text-align: left;
}

.tabcontent::-webkit-scrollbar {
  width: 6px;
}

.tabcontent::-webkit-scrollbar-track {
  background: #eeeeee;
}

.tabcontent::-webkit-scrollbar-thumb {
  background-color: #a5a5a5;
}

@media screen and (max-width: 780px) {
  .container-tab {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    width: 100%;
  }

  .tabtitle {
    display: none;
  }

  .tabcontent {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: calc(100% - 20px);
    padding: 10px;
    height: auto;
    overflow-x: none !important;
    border: 2px solid #eeeeee;
  }

  .tabtitle-mobile {
    display: flex;
    justify-content: space-around;
    flex-direction: row;
    align-content: space-between;
    padding: 10px;
    width: calc(100% - 20px);
  }
}
